rm: scale(var(--#{$fa-css-prefix}-bounce-land-scale-x, 1.05),var(--#{$fa-css-prefix}-bounce-land-scale-y, 0.95)) translateY(0); }\n  57%  { transform: scale(1,1) translateY(var(--#{$fa-css-prefix}-bounce-rebound, -0.125em)); }\n  64%  { transform: scale(1,1) translateY(0); }\n  100% { transform: scale(1,1) translateY(0); }\n}\n\n@keyframes #{$fa-css-prefix}-fade {\n  50% { opacity: var(--#{$fa-css-prefix}-fade-opacity, 0.4); }\n}\n\n@keyframes #{$fa-css-prefix}-beat-fade {\n  0%, 100% {\n    opacity: var(--#{$fa-css-prefix}-beat-fade-opacity, 0.4);\n    transform: scale(1);\n  }\n  50% {\n    opacity: 1;\n    transform: scale(var(--#{$fa-css-prefix}-beat-fade-scale, 1.125));\n  }\n}\n\n@keyframes #{$fa-css-prefix}-flip {\n  50% {\n    transform: rotate3d(var(--#{$fa-css-prefix}-flip-x, 0), var(--#{$fa-css-prefix}-flip-y, 1), var(--#{$fa-css-prefix}-flip-z, 0), var(--#{$fa-css-prefix}-flip-angle, -180deg));\n  }\n}\n\n@keyframes #{$fa-css-prefix}-shake {\n  0% { transform: rotate(-15deg); }\n  4% { transform: rotate(15deg); }\n  8%, 24% { transform: rotate(-18deg); }\n  12%, 28% { transform: rotate(18deg); }\n  16% { transform: rotate(-22deg); }\n  20% { transform: rotate(22deg); }\n  32% { transform: rotate(-12deg); }\n  36% { transform: rotate(12deg); }\n  40%, 100% { transform: rotate(0deg); }\n}\n\n@keyframes #{$fa-css-prefix}-spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n","// rotating + flipping icons\n// -------------------------\n\n.#{$fa-css-prefix}-rotate-90 {\n  transform: rotate(90deg);\n}\n\n.#{$fa-css-prefix}-rotate-180 {\n  transform: rotate(180deg);\n}\n\n.#{$fa-css-prefix}-rotate-270 {\n  transform: rotate(270deg);\n}\n\n.#{$fa-css-prefix}-flip-horizontal {\n  transform: scale(-1, 1);\n}\n\n.#{$fa-css-prefix}-flip-vertical {\n  transform: scale(1, -1);\n}\n\n.#{$fa-css-prefix}-flip-both,\n.#{$fa-css-prefix}-flip-horizontal.#{$fa-css-prefix}-flip-vertical { \n  transform: scale(-1, -1);\n}\n\n.#{$fa-css-prefix}-rotate-by {\n  transform: rotate(var(--#{$fa-css-prefix}-rotate-angle, none));\n}\n","// stacking icons\n// -------------------------\n\n.#{$fa-css-prefix}-stack {\n  display: inline-block;\n  height: 2em;\n  line-height: 2em;\n  position: relative;\n  vertical-align: $fa-stack-vertical-align;\n  width: $fa-stack-width;\n}\n\n.#{$fa-css-prefix}-stack-1x,\n.#{$fa-css-prefix}-stack-2x {\n  left: 0;\n  position: absolute;\n  text-align: center;\n  width: 100%;\n  z-index: var(--#{$fa-css-prefix}-stack-z-index, #{$fa-stack-z-index});\n}\n\n.#{$fa-css-prefix}-stack-1x {\n  line-height: inherit;\n}\n\n.#{$fa-css-prefix}-stack-2x {\n  font-size: 2em;\n}\n\n.#{$fa-css-prefix}-inverse {\n  color: var(--#{$fa-css-prefix}-inverse, #{$fa-inverse});\n}\n","// specific icon class definition\n// -------------------------\n\n/* Font Awesome uses the Unicode Private Use Area (PUA) to ensure screen\nreaders do not read off random characters that represent icons */\n\n@each $name, $icon in $fa-icons {\n  .#{$fa-css-prefix}-#{$name}::before { content: unquote(\"\\\"#{ $icon }\\\"\"); }\n}\n\n","// screen-reader utilities\n// -------------------------\n\n// only display content to screen readers\n.sr-only,\n.#{$fa-css-prefix}-sr-only {\n  @include fa-sr-only;\n}\n\n// use in conjunction with .sr-only to only display content when it's focused\n.sr-only-focusable,\n.#{$fa-css-prefix}-sr-only-focusable {\n  @include fa-sr-only-focusable;\n}\n","/*!\n * Font Awesome Free 6.4.2 by @fontawesome - https://fontawesome.com\n * License - https://fontawesome.com/license/free (Icons: CC BY 4.0, Fonts: SIL OFL 1.1, Code: MIT License)\n * Copyright 2023 Fonticons, Inc.\n */\n@import 'functions';\n@import 'variables';\n\n:root, :host {\n  --#{$fa-css-prefix}-style-family-classic: '#{ $fa-style-family }';\n  --#{$fa-css-prefix}-font-solid: normal 900 1em/1 '#{ $fa-style-family }';\n}\n\n@font-face {\n  font-family: 'Font Awesome 6 Free';\n  font-style: normal;\n  font-weight: 900;\n  font-display: $fa-font-display;\n  src: url('#{$fa-font-path}/fa-solid-900.woff2') format('woff2'),\n    url('#{$fa-font-path}/fa-solid-900.ttf') format('truetype');\n}\n\n.fas,\n.#{$fa-css-prefix}-solid {\n  font-weight: 900;\n}\n","/*!\n * Font Awesome Free 6.4.2 by @fontawesome - https://fontawesome.com\n * License - https://fontawesome.com/license/free (Icons: CC BY 4.0, Fonts: SIL OFL 1.1, Code: MIT License)\n * Copyright 2023 Fonticons, Inc.\n */\n@import 'functions';\n@import 'variables';\n\n:root, :host {\n  --#{$fa-css-prefix}-style-family-classic: '#{ $fa-style-family }';\n  --#{$fa-css-prefix}-font-regular: normal 400 1em/1 '#{ $fa-style-family }';\n}\n\n@font-face {\n  font-family: 'Font Awesome 6 Free';\n  font-style: normal;\n  font-weight: 400;\n  font-display: $fa-font-display;\n  src: url('#{$fa-font-path}/fa-regular-400.woff2') format('woff2'),\n    url('#{$fa-font-path}/fa-regular-400.ttf') format('truetype');\n}\n\n.far,\n.#{$fa-css-prefix}-regular {\n  font-weight: 400;\n}\n","\n//////////////////////////////////////////////////////////////////////////////////////\n// Basic Variables\n//////////////////////////////////////////////////////////////////////////////////////\n$font-color:#666; \n$input-bg: #eee; \n$input-border: #666;\n$label-color:$font-color; \n$fieldset:#666; \n$error: $danger;\n$borders: 1px solid $input-border; \n$easeOutExpo:cubic-bezier(0.19, 1, 0.22, 1); /* easeOutExpo */\n$easeInOutExpo:cubic-bezier(1.000, 0.000, 0.000, 1.000); /* easeInOutExpo */\n\n//////////////////////////////////////////////////////////////////////////////////////\n// FORM RESETS\n//////////////////////////////////////////////////////////////////////////////////////\ninput,\nbutton,\ntextarea,\nselect {\n  border-radius: 0; /* may change, but start consistently */\n  box-sizing: border-box;\n  font-family: inherit;\n  font-size: inherit;\n  font-weight: inherit;\n  line-height: normal;\n  margin: 0;\n  overflow: visible; /* show overflow in Edge */\n  text-transform: none; /* remove inheritance Edge, Firefox, IE */\n}\n\ninput:disabled, input:read-only, textarea:disabled, textarea:read-only {\n  background: $border-color !important;\n  color: darken($font-color, 10%);\n  border: 2px solid darken($border-color, 5%) !important;\n  cursor:not-allowed;\n}\n\nselect[multiple] {\n  -moz-appearance: menulist;\n  -webkit-appearance: menulist;\n  appearance: menulist;\n  overflow: auto;\n  overflow: scroll;\n}\n\ninput::-ms-clear {\n  display: none;\n  height: 0;\n  width: 0;\n}\n\nbutton::-moz-focus-inner {\n  border-style: none;\n  padding: 0;\n}\n\nlegend {\n  color: inherit; /* 1 */\n  display: table; /* 2 */\n  line-height: normal;\n  max-width: 100%; /* 2 */\n  white-space: normal; /* 2 */\n}\n\nh2 { \n  margin-top:0;\n}\n\n\n\n// .select2-container, .select2-container--mfcs {\n//   width:100% !important; \n//   display: inline-block !important;\n//   padding:0;\n\n//   .select2-container--open { \n//    width:100px !important;\n//   }\n\n//   .select2-selection--multiple {\n\n//     // height of input box\n//     min-height: 35px !important;\n    \n//     // input box\n//     .select2-selection__rendered {\n//       box-sizing: border-box;\n//       list-style: none;\n//       margin: 0;\n//       padding: 0 8px;\n//       width: 100%;\n//     }\n\n//     // each selected item in input box\n//     .select2-selection__choice {\n//       color: $dark;\n//       padding: 0;\n//       padding-right: 5px;\n//       cursor: pointer;\n//       float: left;\n//       margin-top: 0.3em;\n//       margin-right: 5px;\n//     }\n\n//     // x button of each selected item in input box\n//     .select2-selection__choice__remove {\n//       color: darken($light, 20%);\n//       font-weight: bold;\n//       margin-left: 3px;\n//       margin-right: 1px;\n//       padding-right: 3px;\n//       padding-left: 3px;\n//       float: left;\n//     }\n\n//     // all clear button\n//     .select2-selection__clear {\n//       // overwrite layout.scss\n//       float: none;\n//       margin-right: 0;\n\n//       position: absolute !important;\n//       top: 0;\n//       right: 0.7em;\n//     }\n//   }\n// }\n\n.select2-container, .select2-container--mfcs {\n  width:100% !important; \n  display: inline-block !important;\n  padding:0;\n}\n\n.select2-container .select2-search--inline { \n  float:unset;\n}\n\n.select2-results { \n  display: block;\n  top: -4px;\n  position: relative;\n  height: 100px;\n  overflow-y: scroll;\n  background:$input-bg !important;\n  border: 2px solid $link-color !important;\n}\n\n.select2-dropdown { \n  appearance: none;\n  border:none; \n  background:unset;\n}\n\n.select2-selection__choice { \n  font-size: 12px;\n  background: $link-color;\n  color:#fff; \n  border-radius: 5px;\n  padding: 5px 10px 5px 5px !important;\n  display:inline-block;\n  margin:5px;\n}\n\n.select2-search { \n  width:100% !important; \n  margin:0;\n}\n\n.select2-container .select2-selection--multiple .select2-selection__rendered { \n  width:100% !important; \n  margin:0; padding:0;\n}\n\n.select2-container .select2-search--inline {\n  .select2-search__field { \n    appearance: none;\n    margin:0; \n    padding: 5px;\n    background:$input-bg !important;\n    border: 2px solid $link-color !important;\n    width:100% !important; \n    background:$input-bg !important;\n    border: 2px solid darken($input-bg, 10%) !important;\n    outline:none;\n\n    input { \n      appearance: none; \n    }\n  }\n}\n\n.select2-container--focus .select2-search--inline {\n  .select2-search__field { \n    border: 2px solid $link-color !important;\n    background: lighten($input-bg,2%)!important;\n  }\n}\n\n\n.select2-results__option { \n  border:1px dotted darken($input-bg,10%) !important;\n}\n\n.select2-container.select2-container--mfcs.select2-container--open { \n  border: 0 !important; \n}\n//////////////////////////////////////////////////////////////////////////////////////\n// FORM PATTERNS\n//////////////////////////////////////////////////////////////////////////////////////\nform{\n  float: left;\n  display: block;\n  // margin-right: 2.3576520234%;\n  width: 100%;\n  position:relative;\n  z-index:0;\n\n  &:last-child {\n    margin-right: 0;\n  }\n\n  * { margin: 0; padding: 0px; }\n\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // FIELDSET\n  //////////////////////////////////////////////////////////////////////////////////////\n  fieldset { \n    padding:1rem; \n    border: $borders; \n    text-align: left;\n    position: relative; \n    margin-top:1rem; \n\n    > legend { \n      padding: 0 1rem;\n      text-transform: uppercase;\n      letter-spacing: 1px;\n      color: $font-color;\n    }\n  }\n\n  .legend { \n    width:100%; \n    display:block; \n    float:left; \n    border: $borders; \n    padding:5px 5px 5px 40px; \n    margin:20px 0;\n    font-size:12px; \n    position:relative; \n    text-align:left; \n\n    span.required { \n      color: $error;\n        font-size:4rem;\n        position:absolute; \n        top:37%; \n        left:10px; \n    }\n  }\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // LABELS\n  //////////////////////////////////////////////////////////////////////////////////////\n  label, .heading { \n    text-align:left;\n    display:inline-block; \n    width:auto;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n    color:$font-color;\n    position: relative;\n    \n    &.required { \n      padding-left:1.5em;\n      &:before { \n        content: \"*\";\n        color: $error;\n        font-size:2.2rem;\n        position: absolute; \n        top:-7px; \n        left:0; \n        padding-left:5px;\n        font-weight:100;\n      }\n    }\n  }\n\n  .heading { \n    text-align: left;\n    display: block;\n    width: 100%;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n    color:$font-color;\n  }\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // BASIC INPUT\n  //////////////////////////////////////////////////////////////////////////////////////\n  input:not([type='submit']){\n      width:100%;\n      padding:.5rem;\n      height:38px;  \n      border:none;\n      background:$input-bg;\n      font-weight:200;\n      letter-spacing: 1px;\n      transition: color 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                  border 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                  background 0.6s cubic-bezier(0.19, 1, 0.22, 1);\n      border: 2px solid darken($input-bg,10%);\n      \n      &:focus, &:hover { \n        border: 2px solid $link-color;\n        background: lighten($input-bg,2%);\n      }\n\n      &::placeholder { \n        color:darken($input-bg,20%);\n      }\n  }\n\n  input[type='file']{\n    height:unset;\n  }\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // TEXTAREA\n  //////////////////////////////////////////////////////////////////////////////////////\n  textarea{\n    width:100%;\n    max-width:100%;\n    border:none;\n    font-size:1rem; \n    line-height:1.5; \n    font-weight:500;\n    letter-spacing: 1px;\n    background:$input-bg;\n    padding:1.25rem;\n    margin-top:.5rem;\n    transition: color 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                border 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                background 0.6s cubic-bezier(0.19, 1, 0.22, 1);\n    border: 2px solid darken($input-bg,10%);\n\n    &:focus, &:hover { \n      border: 2px solid $link-color;\n      background: lighten($input-bg,2%);\n    }\n\n    &::placeholder { \n      color:darken($input-bg,20%);\n    }\n  }\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // FEEDBACK AND VALIDATIONS\n  //////////////////////////////////////////////////////////////////////////////////////\n  input .is-valid, .is-valid, .valid-feedback { \n    border: 2px solid $success !important; \n    &:hover, &:focus { \n      border: 2px solid $success; \n    }\n  }\n\n  input .is-invalid, .is-invalid, .invalid-feedback { \n    border: 2px solid $error !important; \n    color: $error !important;\n    &:hover, &:focus { \n      border: 2px solid $error; \n    }\n  }\n\n  div.valid-feedback, div.invalid-feedback { \n    border:unset; \n  }\n\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // SELECTS\n  //////////////////////////////////////////////////////////////////////////////////////\n  .select-style { \n    font-size:1rem; \n    padding: 0;\n    margin: 0;\n    height:38px; \n    border: none;\n    border-radius: 0;\n    overflow: hidden;\n    background: #eee;\n    position:relative;\n    font-weight:500;\n    letter-spacing: 1px;\n    border: 2px solid darken($input-bg,10%);  \n    transition: color 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                border 0.6s cubic-bezier(0.19, 1, 0.22, 1), \n                background 0.6s cubic-bezier(0.19, 1, 0.22, 1);\n\n    &:focus, &:hover { \n      border: 2px solid $link-color;\n      background: lighten($input-bg,2%);\n    }\n\n    &:before {\n      content: \"\\f078\";\n      color:rgba( #000, .3);\n      font-size: 1.2rem; \n      text-align: center;\n      font-family: 'Font Awesome 5 Free';\n      font-weight: 900;\n      display:block; \n      position:absolute;\n      width:2rem; \n      right:10px;\n      top:calc(50% - .7rem); \n      z-index:0; \n    }\n    select {\n      padding:.25rem; \n      width: 100%;\n      height:38px; \n      font-size:1rem;\n      border: none;\n      box-shadow: none;\n      background-color: transparent;\n      background-image: none;\n      -webkit-appearance: none;\n         -moz-appearance: none;\n              appearance: none;\n      position:relative;\n      z-index:2;\n      color:#000;\n    }\n  }\n\n  //////////////////////////////////////////////////////////////////////////////////////\n  // Form Field Styles\n  //////////////////////////////////////////////////////////////////////////////////////\n  // .field{\n  //   margin-top:1rem;\n  //   clear: both; \n  //   &:first-of-type { \n  //     margin-top:0; \n  //   }\n  // }\n\n  .field_with_errors { \n    input, textarea, .select-style { \n      border: 2px solid $error;\n      background: lighten($error,40%);\n      &:hover, &:focus { \n        border: 2px solid $error;\n        background: lighten($error,40%);\n        outline: none; \n      }\n    }\n    label { color:$error; }\n  }\n\n} // end form\n\n//////////////////////////////////////////////////////////////////////////////////////\n// RAILS ERROR EXPLAINATION FORM\n//////////////////////////////////////////////////////////////////////////////////////\n#error_explanation { \n  float: left;\n  display: block;\n  margin-right: 2.3576520234%;\n  width: 100%;\n  padding:.5rem; \n  color:$error; \n  background: rgba($error, .1); \n  border: 2px solid $error; \n  margin-bottom:2rem; \n  h3 { color:$error; }\n  ul { \n    float: left;\n    display: block;\n    margin-right: 2.3576520234%;\n    width: 100%;\n    padding:1rem;\n    padding-left:2.5rem; \n  }\n}\n\n.actions { \n  float: left;\n  display: block;\n  margin-right: 2.3576520234%;\n  width: 100%;\n  padding:1rem 0;\n  margin-top:1rem; \n  position:relative; \n  input { position: relative; top:-2px; }\n}\n\n//////////////////////////////////////////////////////////////////////////////////////\n// Explanation Text or Microtext used in various parts of forms.\n//////////////////////////////////////////////////////////////////////////////////////\n.micro-text { \n  float: left;\n  display: block;\n  margin-right: 2.3576520234%;\n  width: 100%;\n  padding:1rem .5rem; \n  letter-spacing:1px; \n  font-weight:400; \n  color:#5f5f5f; \n  font-size:.75rem; \n}\n\n\n//////////////////////////////////////////////////////////////////////////////////////\n// CHECKBOX PATTERN - A11Y Project Styles\n//////////////////////////////////////////////////////////////////////////////////////\n// NOTE: THIS IS NOT MY PATTERN.  THIS CONCEPT WAS TAKEN FROM CSS BY THE A11Y PROJECT\n\n$checkbox-color: #444;\n$checkbox-border: 1px solid $checkbox-color;\n$checkbox-box-shadow: 0 0 0 px $checkbox-color;\n\n$check-hover-color: lighten(#002855,10%);\n$checkbox-border-hover: 1px solid $check-hover-color;\n$checkbox-box-shadow-hover: 0 0 0 2px $check-hover-color;\n\n\n.checkbox-wrapper { \n  float: left;\n  display: block;\n  margin-right: 2.3576520234%;\n  width: 100%;\n}\n\n.checkbox {\n  // padding: 2rem;\n  position: relative;\n  // width:100%;\n\n  // @media (min-width: 768px) {\n  //   max-width:50%;\n  // }\n\n  &:focus-within {\n    border: 2px dotted $check-hover-color;\n  }\n\n  .field-help { \n    position: absolute;\n    z-index: 9999;\n    top: 3px;\n    right: 20px;\n  }\n\n  // children of the checkbox class only\n  > {\n    // label\n    label {\n      display: inline-block;\n      padding: 0 0 0 2rem;\n      vertical-align: top;\n      width: auto;\n      padding-left:2rem;\n\n      &:before, &:after {\n        border: 1px solid;\n        content: \" \";\n        position: absolute;\n        transition: border-color .2s ease-in-out, box-shadow .2s ease-in-out, transform .2s ease-in-out;\n      }\n\n      &:before {\n        border: $checkbox-border;\n        border-radius: 1px;\n        box-shadow: $checkbox-box-shadow;\n        height: 1.125rem;\n        left: .125rem;\n        top: .25rem;\n        width: 1.125rem;\n      }\n\n      &:after {\n        border: 0;\n        border-bottom: 2px solid $checkbox-color;\n        border-right: 2px solid $checkbox-color;\n        height: .825rem;\n        left: .425rem;\n        top: .25rem;\n        transform-origin: center center;\n        transform: rotate(45deg) scale(0);\n        width: .5rem;\n      }\n    }\n\n    // input\n    input[type=\"checkbox\"] {\n      -moz-appearance: none;\n      -webkit-appearance: none;\n      appearance: none;\n      opacity: .00001;\n      position: absolute;\n      vertical-align: top;\n      z-index: 2;\n    }\n\n    input {\n      &:checked ~ label:before {\n        border-color: transparent;\n        box-shadow: $checkbox-box-shadow-hover;\n      }\n\n      &:focus ~ label:before {\n        border-color: transparent;\n        box-shadow: $checkbox-box-shadow-hover;\n        outline-offset: 2px;\n        outline: 2px solid transparent;\n      }\n\n      &:focus + label {\n        border-color: transparent;\n        //box-shadow: $checkbox-box-shadow-hover;\n      }\n\n      &:checked {\n        ~ label:after {\n          transform: rotate(45deg) scale(1);\n        }\n        &:focus ~ label:after {\n          border-color: $check-hover-color;\n        }\n      }\n\n      &[disabled] ~ label {\n        opacity: .625;\n      }\n      \n      &:invalid {\n        ~ label:before {\n          border-color: #f00;\n          box-shadow: 0 0 0 1px #f00;\n        }\n        &:focus ~ label:before {\n          box-shadow: 0 0 0 2px #f00;\n        }\n      }\n    }\n  }\n}\n\n//////////////////////////////////////////////////////////////////////////////////////\n// RADIO BUTTON PATTERN - A11Y Project Styles\n//////////////////////////////////////////////////////////////////////////////////////\n// NOTE: THIS IS NOT MY PATTERN.  THIS CONCEPT WAS TAKEN FROM CSS BY THE A11Y PROJECT\n\n$radio-color: #444;\n$radio-border: 1px solid $radio-color;\n$radio-box-shadow: 0 0 0 2px $radio-color;\n\n$radio-hover-color: lighten(#002855,10%);\n$radio-border-hover: 1px solid $radio-hover-color;\n$radio-box-shadow-hover: 0 0 0 2px $radio-hover-color;\n\n.radiobutton {\n  max-width:100%;\n\n  @media (min-width: 768px) {\n    max-width:50%;\n  }\n\n  &:focus-within {\n    border: 2px dotted $radio-hover-color;\n  }\n}\n\n.radio-group {\n  position: relative;\n  display: inline-block;\n\n  > {\n\n    input[type=\"radio\"] {\n      -moz-appearance: none;\n      -webkit-appearance: none;\n      appearance: none;\n      background: none;\n      opacity: .00001;\n      z-index: 2;\n      border: 1px solid;\n      height: 1.125rem;\n      left: .125rem;\n      position: absolute;\n      top: 1rem;\n      width: 1.125rem;\n      border-radius: 100%;\n      content: \" \";\n      transition: border-color .2s ease-in-out, box-shadow .2s ease-in-out;\n    }\n\n    label {\n      display: inline-block;\n      padding: .75rem .5rem .75rem 2rem;\n\n      &:before, &:after {\n        border: 1px solid;\n        height: 1.125rem;\n        left: .125rem;\n        position: absolute;\n        top: 1rem;\n        width: 1.125rem;\n      }\n\n      &:before {\n        border-radius: 100%;\n        content: \" \";\n        transition: border-color .2s ease-in-out, box-shadow .2s ease-in-out;\n        border-color: transparent;\n        box-shadow: 0 0 0 0px rgba(0, 0, 0, 0);\n      }\n\n      &:after {\n        border-radius: 100%;\n        content: \" \";\n        transition: border-color .2s ease-in-out, box-shadow .2s ease-in-out;\n        border-color: $radio-color;\n      }\n    }\n\n    input {\n      &:checked ~ label:before {\n        border-color: transparent;\n        box-shadow: $radio-box-shadow-hover;\n      }\n      &:focus ~ label:before {\n        border-color: transparent;\n        box-shadow: $radio-box-shadow-hover;\n      }\n      &:checked ~ label:after {\n        border-color: #fff;\n        border-width: 1px;\n        box-shadow: inset 0 0 0 4px $radio-hover-color;\n      }\n      &:focus ~ label {\n        color: $radio-hover-color;\n      }\n      &[disabled] ~ label {\n        opacity: .5;\n      }\n    }\n\n  } // end child\n} // end radio-group\n\n@media screen and (-ms-high-contrast: active) {\n  .radio-group > input:checked ~ label:before {\n    background: window;\n    border: 6px solid buttonface;\n    box-shadow: none;\n  }\n}",".card .btn-default { \n  color: $link; \n  &:hover, &:active, &:focus { \n    box-shadow: inset 0 0 0 2px $lin